import Vue from 'vue'
import store from './store'
import Router from 'vue-router'
import Layout from './views/layout/layout'

const Login =     () => import("@/views/login")
const Home =     () => import('@/views/home')

Vue.use(Router)

export const unLoginRouters = [

]

export const needLoginRouters = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title: "登录页",
    }
  },
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    meta: {
      title: "主页",
    }
  },
]

export const Routes = unLoginRouters.concat(needLoginRouters)

export default new Router({
  //mode: "history",
  routes:Routes,
  scrollBehavior (to, from, savedPosition) {
    // if (savedPosition) {
    //   store.commit('setScrollTop',savedPosition.y)
    //   return savedPosition
    // } else {
    //   return { x: 0, y: 0 }
    // }
    return { x: 0, y: 0 }
  },
})
