let tipTimer = null, tipEle = null
export function tips(text) {
    if (!tipEle) {        
        tipEle = document.createElement("div")
        tipEle.style.display = "none"
        
        document.body.appendChild(tipEle)
    }
    clearTimeout(tipTimer)
    tipEle.className = "tips"
    tipEle.textContent = text
    tipEle.style.display = "block"
    tipTimer = setTimeout(_ => {
        tipEle.style.display = "none"
    }, 3000)
}

tips.success = text => {
    if (!tipEle) {        
        tipEle = document.createElement("div")
        tipEle.style.display = "none"
        document.body.appendChild(tipEle)
    }
    clearTimeout(tipTimer)
    tipEle.className = "tips_success"
    tipEle.textContent = text
    tipEle.style.display = "block"
    tipTimer = setTimeout(_ => {
        tipEle.style.display = "none"
    }, 3000)
}