import Vue from 'vue'
// state
const state = {
  isLarge:true,
}



// getters
const getters = {
  isLarge(state) {
    return state.isLarge
  }
}

// mutations
const mutations = {
  setIsLarge(state, bool){
    state.isLarge = bool
  },
}

// actions
const actions = {

}

export default {
  state,
  getters,
  actions,
  mutations
}