<template>
  <nav>
    <div :class="{'curr':isCurr('VoiceReg')}" @click="goto('/voiceReg')">
      <i class="iconfont iconzhuce2"></i>
      <p>声纹注册</p>
    </div>
    <div :class="{'curr':isCurr('Reccognition')}" @click="goto('/reccognition')">
      <i class="iconfont icondanren"></i>
      <p>声纹识别</p>
    </div>
    <div :class="{'curr':isCurr('MultipleReccognition')}" @click="goto('/multipleReccognition')">
      <i class="iconfont iconduoren"></i>
      <p>对话识别</p>
    </div>
  </nav>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

export default {
  name: "AppMenu",
  data() {
    return {
    };
  },
  computed: {
    
  },
  created() {},
  methods: {
    isCurr(key){
      var name = this.$route.name
      return name === key
    },
    goto(path){
      var routePath = this.$route.path
      if(routePath === path) return
      this.$router.push(path)
    }
  },
  components: {}
};
</script>
<style lang="scss" scoped>
nav{
  background:#fff;
  display:flex;
  width:100%;
  border-top:1px solid #e7e7e7;
  >div{
    cursor: pointer;
    width:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:0.24rem 0;
    i{
      font-size:0.44rem;
      margin-bottom:0.1rem;
    }
    &.curr{
      background:$secondPrimary;
      color:#fff;
    }
  }
}
</style>
