import Cookies from 'js-cookie'
import router from './router'
import store from './store'

import NProgress from 'nprogress' // Progress 进度条

const isParentToSon = (to, from) => {
    let result = false;
    if(to.meta.keepAliveParents){
        result = to.meta.keepAliveParents.includes(from.name)
    }
    return result
}

const isSonToParent = (to, from) => {
    let result = false;
    if(to.meta.keepAliveChildren){
        result = to.meta.keepAliveChildren.includes(from.name)
    }
    return result
}

router.beforeEach((to, from, next) => {
    NProgress.start()
    var isLogin = ![null,undefined,""].includes(Cookies.get("voiceUser"))

    isLogin = true
    
    if(!isLogin && to.name !== "Login"){
        next('/login')
        NProgress.done()
        return
    }
    if(isLogin && to.name === "Login"){
        next(from)
        NProgress.done()
        return
    }

    let keepalive = JSON.parse(JSON.stringify(store.getters.keepalive))
    if(isParentToSon(to, from) && isSonToParent(to, from)){
        store.commit('setKeepalive',[])
        next()
        return
    }
    if(isParentToSon(to, from)){
        if(!keepalive.includes(from.name)){
            keepalive.push(from.name)
            store.commit('setKeepalive',keepalive)
        }
        next()
        return
    }
    if(isSonToParent(to, from)){
        if(!keepalive.includes(to.name)){
            store.commit('setKeepalive',[])
        }
        next()
        return
    }
    store.commit('setKeepalive',[])
    next()
})

router.afterEach((to,from) => {
    NProgress.done()
})