<template>
  <header>
    <a href="javascript:;"><i class="iconfont icontuichu" @click="pageLogout"></i></a><span>{{userInfo.name}}</span>
  </header>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

export default {
  name: "AppMenu",
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters(["userInfo"])
  },
  created() {},
  methods: {
    ...mapMutations(["logout"]),
    pageLogout() {
      if(!window.confirm("退出登录, 是否继续?")){
        return
      }
      this.logout()
      this.$router.push("/login");
    }
  },
  components: {}
};
</script>
<style lang="scss" scoped>
header{
  width:100%;
  display:flex;
  flex-direction:row-reverse;
  align-items: center;
  padding:0.08rem 0.12rem;
  i{
    font-size:0.40rem;
    margin-left:0.2rem;
  }
}
</style>
