import qs from 'qs'
import Cookies from 'js-cookie'
import axios from 'axios'
import Vue from 'vue'
import { tips } from '../util/tips'

//let baseURL = "/mock/20/creditGetting"

let baseURL = "/aivoice"

const headers = {
  'Content-Type': 'application/json;charset=UTF-8'
}

let instance = axios.create({
	headers,
  baseURL: baseURL,
  timeout: 30000,
  withCredentials:true,
  transformResponse: [function (json) {
    json = instance.transformNumber(json);
    try{
      json = JSON.parse(json)
    }
    catch(e){console.log('axios transformResponse:' + e.message)}
    return json
  }],
});

instance.transformNumber = (str) => {
  return str.replace(/("(?:\\u[\da-f]{4}|\\[\d\D]|[^\\"])*")|([^"]+)/ig,
    function($, $1, $2) { if ($1) return $1; return $2.replace(/[\d\.-]{16,}/g, '"$&"');
  })
}


const isEmptyObj = (obj) => {
  for(var key in obj){
    return false
  }
  return true
}



//POST传参序列化
instance.interceptors.request.use((request) => {
  if(request.data instanceof FormData){
    return request
  }
  if(request.method === 'get'){
    request.params = {
      ...request.params,
      _t: new Date().getTime()
    }
  }
  else if(request.method === 'post') {
    if(!isEmptyObj(request.params)){
      request.params = {
        ...request.params,
        _t: new Date().getTime()
      }
    }
    request.data = {
      ...request.data,
    }
  } 
  return request;
},(error) =>{
  return Promise.reject(error);
});

//返回状态判断

//需要返回lastModified参数的接口都要进白名单
var whiteList = [/periodical\/list/,/periodical\/save/];
var lastTime = new Date().getTime()

instance.interceptors.response.use(res =>{
  let isInWhiteList = whiteList.some(item => {
    return item.test(res.config.url)
  })
  if(isInWhiteList){
    res.data.lastModified = res.headers["last-modified"]
  }
  if(res.data){return res.data};
  return res;
  }, (error) => {
  if(!error.response){
    tips("您的网络出现故障，请稍后再试")
    return
  }
  //404等问题可以在这里处理
  if(error.response.status === 401){
    tips("请先登录")
    Cookies.remove('voiceUser');
    location.href = "/"
  }
  if(error.response.status >= 500){
    var currTime = new Date().getTime()
    var diff = currTime - lastTime
    lastTime = currTime
    //console.log(diff)
    if(diff < 200) return
    tips("服务器暂不可用，请稍后再试")
  }
  else if(error.response.status >= 400){
    tips(error.response.data.message)
  }
  return Promise.reject(error);
});

export default instance
