import Vue from 'vue'
import Cookies from 'js-cookie'
import { login } from "@/api/login";

// state
const state = {
  userInfo: {
    id: null,
    name: null
  }
}



// getters
const getters = {
  isLogin(state) {
    let voiceUser_SESSION = Cookies.get("voiceUser")
    let isLogin = (voiceUser_SESSION != null && voiceUser_SESSION != "")
    return isLogin
  },
  userInfo(state) {
    return state.userInfo
  }
}

// mutations
const mutations = {
  setUserInfo(state, userInfo) {
    state.userInfo = userInfo
  },
  delCookie(state) {
    Cookies.remove("voiceUser")
  },
  delUserInfo(state) {
    state.userInfo = {
      id: null,
      name: null,
    }
  },
  logout() {
    this.commit("delCookie")
    this.commit("delUserInfo")
  }
}

// actions
const actions = {
  login({ commit }, { account, password }) {
    return new Promise((resolve, reject) => {
      login({ account, password })
        .then(res => {
          if (res.success) {
            commit('setUserInfo', res.data)
            resolve()
          }
          else {
            reject()
          }
        })
        .catch(e => {
          reject()
        })
    })
  },
}

export default {
  state,
  getters,
  actions,
  mutations
}