<template>
  <section>
    <transition name="fade" mode="out-in" @after-enter="afterEnter">
      <keep-alive :include="keepaliveViews">
        <router-view></router-view>
      </keep-alive>
    </transition>
  </section>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

export default {
  name: 'AppMain',
  data(){
    return {
    }
  },
  computed: {
    ...mapState({
      keepaliveViews: state => state.views.keepalive,
      scrollTop: state => state.ui.scrollTop,
    }),
  },
  methods:{
    afterEnter(){
      let scrollTop = 0
      if(this.keepaliveViews.includes(this.$route.name)){
        scrollTop = this.scrollTop
      }
      document.documentElement.scrollTop = scrollTop
      document.body.scrollTop = scrollTop
    }
  }
}
</script>

<style lang="scss" scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity,transform .2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform:translateX(30px);
}
</style>
