<template>
  <div class="app-absolute">
    <div class="app-height100">
      <div class="app-wrapper">
        <div class="header">
          <app-header></app-header>
        </div>
        <div class="main" ref="main">
          <app-main></app-main>
        </div>
        <div class="footer">
          <app-footer></app-footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
import AppHeader from "./appHeader";
import AppMain from "./appMain";
import AppFooter from "./appFooter";

export default {
  name: "Layout",
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["userInfo"])
  },
  created() {},
  methods: {
    ...mapMutations([]),
  },
  beforeRouteLeave(to, from, next){
    let main = this.$refs.main
    main.scrollTop = 0
    next()
  },
  components: {
    AppHeader,
    AppMain,
    AppFooter
  }
};
</script>

<style lang="scss" scoped>
.app-absolute {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  .app-height100{
    height: 100%;
  }
}
.app-wrapper {
  display:flex;
  flex-direction:column;
  height:100%;
  .header{
    display:flex;
    position:relative;
  }
  .main {
    flex:1;
    overflow-x:hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling:touch;
    @include scrollBar;

  }
  .footer {
    display:flex;
    position:relative;
  }
}
</style>
