import Vue from 'vue'

// state
const state = {
  keepalive:[],
}



// getters
const getters = {
  keepalive(state){
    return state.keepalive
  },
}

// mutations
const mutations = {
  setKeepalive(state,keepalive){
    Vue.set(state, "keepalive", keepalive)
  },
}

// actions
const actions = {

}

export default {
  state,
  getters,
  actions,
  mutations
}